import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import HeroHeader from '../components/header/useHeroHeader';
import Layout from '../components/layout/layout2';

function News() {
    const [list, setList] = useState([
        {
            image: "https://guardian.ng/wp-content/uploads/2020/05/Multichoice-1.jpg",
            author: "Obinna Nwaoku",
            date: "14 July 2022",
            title: "Pinheiro faults report on Appeal Court judgment involving MultiChoice, Multi Digital Limited",
            abstract: "Port Harcourt Divison of Appeal Court, yesterday, reportedly ordered MultiChoice, owners of DSTV and GoTV, to sub-license some television channels to Metro Digital Limited, within 21 days, according to the Nigerian Broadcasting Commission’s (NBC) code."
        },
        {
            image: "https://guardian.ng/wp-content/uploads/2020/09/Court-Justice-Gavel-974x546.jpg",
            author: "Osiberoha Osibe",
            date: "02 December 2021",
            title: "Appeal Court sacks Ejidike, reinstates Ibe as Anambra APC chair",
            abstract: "Court of Appeal in Enugu has sacked suspended Chairman of the Anambra State chapter of the All Progressives Congress (APC), Basil Ejidike, and reinstated his predecessor, Emeka Ibe as party chairman."
        },
        {
            image: "https://guardian.ng/wp-content/uploads/2021/03/Court-of-Appeal.jpg",
            author: "Azimazi Momoh Jimoh",
            date: "30 October 2021",
            title: "Jubilation in PDP as Appeal Court okays convention",
            abstract: "The Peoples Democratic Party (PDP) is in jubilant mood, following the dismissal by the Court of Appeal sitting in Port Harcourt, Rivers State, of the application by the former National Chairman, Uche Secondus, seeking suspension of the party’s convention billed for today."
        },
        {
            image: "https://guardian.ng/wp-content/uploads/2020/07/Gavel-.jpg",
            author: "Bertram Nwannekanma",
            date: "04 May 2021",
            title: "Appeal court quashes conviction of Umar for armed robbery",
            abstract: "The Court of Appeal sitting in Gombe has quashed and set aside the death sentence handed to one Sale Umar by Justice Beatrice Iliya of Gombe High Court for conspiracy and armed robbery."
        },
        {
            image: "https://guardian.ng/wp-content/uploads/2020/07/b59d39ba-gov-akeredolu-10.jpg",
            author: "Adamu Abuh",
            date: "17 June 2021",
            title: "Appeal Court reaffirms Akeredolu’s victory in guber poll",
            abstract: "The Court of Appeal sitting in Akure has upheld the election of Governor Oluwarotimi Akeredolu and his deputy, Lucky Aiyedatiwa, in the Ondo State October 10, 2020 election, re-affirming that the appellant’s case, Eyitayo Jegede, lacks merit."
        },
        {
            image: "https://guardian.ng/wp-content/uploads/2020/03/court-of-appeal-1-974x548.jpg",
            author: "Silver Nwokoro",
            date: "08 June 2021",
            title: "Appeal court discharges, acquits armed robbery convict",
            abstract: "Justice Ebiowei Tobi of the Court of Appeal, Gombe has discharged and acquitted Jeremiah Stephen (appellant) for the offences of conspiracy and armed robbery."
        },
    ]);
    useEffect(() => {
        document.title = "News";
    });

    return (
        <>
            <Layout>
                <HeroHeader name={"News"} />
                <div className="blog-area ptb-100">
                    <div className="container">
                        <div className="row">
                            {list.map((item) =>
                                <div className="col-lg-4 col-sm-6">
                                    <div className="blog-card">
                                        <Link to={`/news-details`}>
                                            <img src={item.image} alt="Image" />
                                        </Link>
                                        <div className="blog-card-text">
                                            <h3><Link to={`/news-details`}>{item.title}</Link></h3>
                                            <ul>
                                                <li>
                                                    <i className="las la-calendar"></i>
                                                    {item.date}
                                                </li>
                                                <li>
                                                    <i className="las la-user-alt"></i>
                                                    {item.author}
                                                </li>
                                            </ul>

                                            <p>{item.abstract}</p>

                                            <Link to={`/news-details`} className="read-more">
                                                Read More <i className="las la-angle-double-right"></i>
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            )}
                        </div>
                        <div className="col-lg-12 col-md-12">
                            <ul className="pagination">
                                <li className="page-item">
                                    <a className="page-link" href="#" aria-label="Previous">
                                        <span aria-hidden="true">&laquo;</span>
                                    </a>
                                </li>
                                <li className="page-item active"><a className="page-link" href="#">1</a></li>
                                <li className="page-item"><a className="page-link" href="#">2</a></li>
                                <li className="page-item"><a className="page-link" href="#">3</a></li>
                                <li className="page-item">
                                    <a className="page-link" href="#" aria-label="Next">
                                        <span aria-hidden="true">&raquo;</span>
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </Layout>
        </>
    );
}

export default News